
import React from "react";
import { Row, Col } from "reactstrap";
import ImageComponent from "./image-component";
import PageTitle from "./page-title";
import ProjectDescription from "./project-description";

const PennywiseApp = () => {
  const desc = `Pennywise is a new startup related to financial domain. This app
  provision the user to invest in mutual funds and have features like
  Video KYC Verification, Bank Account Verification, User Authentication
  using Google Firebase Auth Services.This app is built on Flutter
  framework and uses Node JS as backend for providing Rest API's. The
  idea behind the app is that to make investment journey of user
  auotomatic.The app automatically invest the change amount from any
  digital transaction done by user via Paytm or any mobile wallet to
  mutual fund automatically. It does so by keeping a track of
  transaction through user SMS's.
  Video KYC verification system has also been implemented via the use of
  Signzy API's.
  Flutter is a framwork which simplifies the process of developing cross
  platform application. Both Android and Ios APP can be devloped using a
  single codebase using flutter. Feel free to check the app on
  playstore.`;

  const imageArray = [
    "/assets/pennywise-project/pennywise_image1.JPG",
    "/assets/pennywise-project/welcomescreens.png",
    "/assets/pennywise-project/image2.png",
    "/assets/pennywise-project/image3.png",
    "/assets/pennywise-project/image4.png",
    "/assets/pennywise-project/image5.png"
  ];
  return (
    <section className="portfolio-section">
      <PageTitle title="Pennywise Mobile App"></PageTitle>
      <div className="portfolio-main container">
        <Row>
          <Col md="8">
            <div className="project-images">
              <ImageComponent images={imageArray}></ImageComponent>
            </div>
          </Col>
          <Col md="4">
            <ProjectDescription
              desc={desc}
              skills="Flutter Framework, Node JS, Express, MySQL"
              website='https://www.pennywiseapp.in'
              app='https://play.google.com/store/apps/details?id=com.pennywise.app'
            ></ProjectDescription>
          </Col>
        </Row>
      </div>
    </section>
  );
};
export default PennywiseApp;
