import React from "react";

const PageTitle = ({ title }) => {
  return (
    <div className="page-title">
      <div class="container">
        <div class="row">
          <div class="col-md-12">
            <h4>{title}</h4>
          </div>
        </div>
      </div>
    </div>
  );
};
export default PageTitle;
