import React from "react";
import { Row, Col } from "reactstrap";
import ImageComponent from "./image-component";
import PageTitle from "./page-title";
import ProjectDescription from "./project-description";

const ApiManager = () => {
  const desc = `API manager is a web app integrated with Azure API manager to help
  clients to regsiter their account in Azure in order to use Azure API
  manager. User can see their API subscription in subscription tab. They can generate API KEY's for the API's which they are
  subscribed to in Azure, it also shows a detailed view of the API limit
  and bandwidth which a client has consumed in reports tab. It is developed on Dot Net
  Core MVC platform and uses Dot NET Core Web API for internal services,
  uses SQL server for storing data.`;

  const imageArray = [
    "/assets/portal/image1.JPG",
    "/assets/portal/image2.JPG",
    "/assets/portal/image3.JPG"
  ];
  return (
    <section className="portfolio-section">
      <PageTitle title="API Manager Web Application"></PageTitle>
      <div className="portfolio-main container">
        <Row>
          <Col md="8">
            <div className="project-images">
              <ImageComponent images={imageArray}></ImageComponent>
            </div>
          </Col>
          <Col md="4">
            <ProjectDescription
              desc={desc}
              skills="Dot Net Core MVC, Web API, Azure APIM, SQL Server"
            ></ProjectDescription>
          </Col>
        </Row>
      </div>
    </section>
  );
};
export default ApiManager;
