import React from "react";
const ProjectDescription = ({ desc, skills, website, app }) => {
  return (
    <div>
      <span>
        <p>{desc}</p>
        <p>
          <br></br>
        </p>
      </span>
      <span>
        <ul className="portfolio-project-data">
          <li>
            <span className="project-folio-span">SKILLS</span>
            <span className="project-folio-info">{skills}</span>
          </li>
          {website && (
            <li>
              <span className="project-folio-span">
                <a href={website} target="_blank">
                  Visit Website
                </a>
              </span>
            </li>
          )}
          {app && (
            <li>
              <span className="project-folio-span">
                <a href={app} target="_blank">
                  Visit Playstore
                </a>
              </span>
            </li>
          )}
        </ul>
      </span>
    </div>
  );
};
export default ProjectDescription;
