import { React, useState } from "react";
import { Row, Col } from "reactstrap";
const ProjectSlider = () => {
  return (
    <Row>
      <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
        <a href="/#/mystore" target="_blank">
          <div className="service-box project-box">
            <div>
              <img
                src="/assets/store-management-project/mystore_dashboard.png"
                alt="no image"
              ></img>
            </div>
            <div className="service-title">
              My Store Web Application
              <span>
                A Store Management application devoped for a client who manages
                a hardware/general store.
              </span>
            </div>
          </div>
        </a>
      </Col>
      <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
        <a href="/#/pennywise-app" target="_blank">
          <div className="service-box project-box">
            <div>
              <img
                src="/assets/pennywise-project/pennywise_0.png"
                alt="no image"
                style={{ width: "60%", height: "auto" }}
              ></img>
            </div>
            <div className="service-title">
              Pennywise Mobile App
              <span>
                Mobile App developed for new startup Pennywise.This app is built
                on Flutter framework and uses Node JS as backend for providing
                Rest API's.
              </span>
            </div>
          </div>
        </a>
      </Col>
      <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
        <a href="/#/api-manager" target="_blank">
          <div className="service-box project-box">
            <div>
              <img src="/assets/portal/image5.JPG" alt="no image"></img>
            </div>
            <div className="service-title">
              API Manager
              <span>
                API manager is a web app integrated with Azure API which enables
                client to register their account in Azure and check their API
                subscriptions.
              </span>
            </div>
          </div>
        </a>
      </Col>
    </Row>
  );
};
export default ProjectSlider;
