import React from "react";
import { Row, Col } from "reactstrap";
import ImageComponent from "./image-component";
import PageTitle from "./page-title";
import ProjectDescription from "./project-description";

const MyStore = () => {
  const desc = `A Store Management application developed for a client who
  manages a hardware/general store. This application provides a
  owner to add their employees in the system,define their roles,
  add products and add customers. It also generates the invoice
  for customers sales order by creating a invoice based on the
  purchase order of the customer and invoice can be printed or
  downloaded as PDF.`;

  const imageArray = [
    "/assets/store-management-project/image_landing.png",
    "/assets/store-management-project/mystore_dashboard.png",
    "/assets/store-management-project/mystore_invoice.png",
    "/assets/store-management-project/mystore_invoice1.png",
    "/assets/store-management-project/mystore_Search.png",
    "/assets/store-management-project/image5.png",
    "/assets/store-management-project/image6.png",
    "/assets/store-management-project/image7.png",
    "/assets/store-management-project/image1.png",
    "/assets/store-management-project/image2.png",
    "/assets/store-management-project/image3.png",
    "/assets/store-management-project/image4.png"
  ];
  return (
    <section className="portfolio-section">
      <PageTitle title="My Store Web Application"></PageTitle>
      <div className="portfolio-main container">
        <Row>
          <Col md="8">
            <div className="project-images">
              <ImageComponent images={imageArray}></ImageComponent>
            </div>
          </Col>
          <Col md="4">
            <ProjectDescription
              desc={desc}
              skills="ReactJS, Redux, NodeJS, Express, MySQL Server"
            ></ProjectDescription>
          </Col>
        </Row>
      </div>
    </section>
  );
};
export default MyStore;
