import React from "react";
import { Row, Col } from "reactstrap";

const ImageComponent = ({ images }) => {
  let img = images.map((image, key) => {
    return (
      <div>
        <img
          style={{
            maxWidth: "100%",
            height: "auto"
          }}
          src={image}
          alt="no image"
        />
      </div>
    );
  });

  return (
    <div>{img}</div>
    // <div style={{marginTop:'30px'}}>
    //   <Row>
    //     <Col style={{ maxWidth: width, maxHeight: height, margin: "auto" }}>
    //       <img
    //         style={{
    //           width: "100%",
    //           height: "100%"
    //         }}
    //         src={image}
    //         alt="no image"
    //       />
    //     </Col>
    //   </Row>
    //   <Row>
    //     <Col>
    //       <div style={{ textAlign: "center" }}> {desc}</div>
    //     </Col>
    //   </Row>
    // </div>
  );
};
export default ImageComponent;
