import { React, useEffect } from "react";
import { Row, Col } from "reactstrap";

const ServicesOffered = () => {
  useEffect(() => {}, []);
  const changeColor = (e) => {};
  return (
    <div>
      <Row>
        <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
          <div className="service-box" onMouseEnter={(e) => changeColor(e)}>
            <div>
              <img src="../assets/webapp.png" alt="no image"></img>
            </div>
            <div className="service-title">
              Web Development
              <span>
                Enterprise Web Application Development frontend to backend using
                .Net MVC/Core, Node JS, React JS
              </span>
            </div>
          </div>
        </Col>
        <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
          <div className="service-box">
            <div>
              <img src="../assets/api.png" alt="no image"></img>
            </div>
            <div className="service-title">
              Rest API Development
              <span>Rest API development using .Net Web API / Node JS</span>
            </div>
          </div>
        </Col>
        <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
          {" "}
          <div className="service-box">
            <div>
              <img src="../assets/database.png" alt="no image"></img>
            </div>
            <div className="service-title">
              Database Designing
              <span>
                Database architecture and designing for applications. Expertise
                in MySQL, SQL Server, Postgres, MongoDB
              </span>
            </div>
          </div>
        </Col>
        <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
          <div className="service-box">
            <div>
              <img src="../assets/mobile.png" alt="no image"></img>
            </div>
            <div className="service-title">
              Mobile App Development
              <span>
                Cross platform mobile APP development for both Android &amp; IOS
                using Flutter framework
              </span>
            </div>
          </div>
        </Col>
        <Col md="4" sm="6" style={{ paddingLeft: "0px", paddingRight: "0px" }}>
          <div className="service-box">
            <div>
              <img src="../assets/lightbulb.png" alt="no image"></img>
            </div>
            <div className="service-title">
              Analytics
              <span>
                Get insights of who is browsing your website using Google Analytics
              </span>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default ServicesOffered;
